<template>
  <v-container fluid class="down-top-padding">
      <class-list-table :activeTeacher="activeTeacher" :item="classListItem" v-if="classList" />
    <v-row v-else>

        <v-col cols="12" sm="12">

            <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
        append-icon="mdi-search"
        label="Search"
        hint="retmenin Adn,Soyadn veya Telefon numarasn arayabilirsiniz."
       hide-default-footer
      ></v-text-field>
    </v-card-title>
    <h6 class="subtitle-2 font-weight-light ml-3">Okulunuzdaki retmenleri dzenleyebilir, ekleyebilirsiniz ve retmenin snf listesini grebilirsiniz</h6>
    <div class="text-right">
          <v-btn small class="ma-2" @click="$router.push('/pages/insert-teacher')" color="primary" dark>{{$t('sidebar.insertTeacher')}}</v-btn>
    </div>
    <v-data-table
    :loading="loadingData"
      :headers="headers"
      :items="teachers"
      :search="search"
    >
    <template v-slot:item.edit="{ item }">
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="setEditUser(item)">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Dzenle</span>
        </v-tooltip>
           <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
       <span @click="setClassList(item)">
 <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-clipboard-list</v-icon>
       </span>
      </template>
      <span>Snf Listesi</span>
        </v-tooltip>
    </template>
    </v-data-table>
    <v-pagination v-model="page" :length="pageCount"></v-pagination>
  </v-card>
      </v-col>
    </v-row>
  <loading-dialog :dialogLoading="dialogLoading">retmen Detay Ykleniyor</loading-dialog>
  <edit-user-dialog :index="editIndex"  @setPutUser="setEditTeacher=$event" :user="editUser" @dialog="editDialog=$event;snackbar=true" :dialog="editDialog" />
  <snack-bar :snackbar="snackbar" @finished="snackbar=$event" />
  </v-container>
</template>

<script>
import loadingDialog from '../../components/dialogs/loadingDialogs'
import editUserDialog from '../../components/dialogs/editUserDialogs'
import classListTable from '../../components/class-list'
export default {
components:{
  classListTable,
  loadingDialog,
  editUserDialog,
},
  name: "Alerts",
watch:{
   setEditTeacher(data){
    if(data!=null){
      this.teachers[data.index].name = data.user.name
      this.teachers[data.index].lastName = data.user.lastName
      this.teachers[data.index].phone = data.user.phone
     if(data.image!=''){
        this.teachers[data.index].image = data.image
     }
      this.setEditTeacher=null
    }
  },
    page(data){
 this.initTeachers(data)
    },
    search(data){
        if(data.length>3){
             this.loadingData=true
          this.$api.get('user?filter=isTeacher=1&sort=id&order=desc&searchFields=name,lastName,phone&search="'+data+'"').then(res=>{
          if(res.count!=0){
              this.loading = false
              this.teachers = res.body
              this.pageCount = res.count
                this.loadingData=false
          }
          else{
              this.pageCount = this.page
                this.loadingData=false
          }
      })
        }
        else{
            this.initTeachers(1)
        }
    }
},
  data: () => ({
    setEditTeacher:null,
    editIndex:-1,
     dialogLoading:false,
     editDialog:false,
      loading:true,
      snackbar:false,
      page:1,
      loadingData:true,
      pageCount:0,
      teachers:[],
      activeTeacher:{},
      headers: [
          {
            text: "retmen Ad",
            align: 'start',
            filterable: true,
            value: 'name',
          },
          { text: 'Soyad', value: 'lastName' },
          { text: 'Telefon', value: 'phone' },
          { text: 'Dzenle', value: 'edit' },
        ],
        search:"",
        editUser:{},
        classList:false,
        classListItem:[]
  }),
  methods:{
    setClassList(row){
      this.classList = true
      this.$api.get('i/j/w/student/user?filter=teacherId='+row.id+'&on=student.parentId=t2.id&fields=student.*,t2.name AS parentName,t2.lastname AS parentLastname,t2.phone,t2.isActive AS parentActive&limit=10&offset=0').then(result=>{
        if(result.count!=0){
          this.activeTeacher = row
          this.classListItem = result.body
        }
        else{
          this.classListItem=[]
        }
      })
    },
    setEditUser(row){
      let pos = this.teachers.map(function(e) { return e.id; }).indexOf(row.id)
     this.editUser = {
             name : row.name,
             lastName : row.lastName,
             id:row.id,
             username : row.username,
             phone:row.phone,
             isTeacher:1,
             image:row.image,
             address:row.address,
          //   index:pos
           }
        this.editIndex=pos
      this.dialogLoading = true
      setTimeout(() => {
        this.dialogLoading = false
        this.editDialog = true
      }, 1000);
    },
      initTeachers(data){
          this.loadingData=true
          this.$api.get('user?filter=isTeacher=1&sort=id&order=desc&limit=10&offset='+(10*(data-1))).then(res=>{
          if(res.count!=0){
            res.body.forEach(f=>{
              f.image = f.image.replace('public/',this.$api.BASE_API_URL)
            })
              this.loading = false
              this.teachers = res.body
              this.pageCount = res.count
              this.loadingData=false
          }
          else{
              this.pageCount = this.page
                this.loadingData=false
          }
      })
      }
  },
  mounted(){
    this.initTeachers(1)
  }
};
</script>
