<template>
  <v-container fluid class="down-top-padding">
    <v-row>
      <v-col cols="12" class="text-right">
        <v-btn color="primary" @click="$router.push('/pages/insert-parent')">Yeni renci Ekle</v-btn>
      </v-col>
        <v-col cols="12" sm="12">
            <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
        append-icon="mdi-search"
        hint="renci Ad veya Soyad, Veli Ad,Soyad veya Telefon numaras arayabilirsiniz."
        label="Search"
       hide-default-footer
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="item"
      :search="search"
    >
     <template v-slot:top>
       <h4 class="ml-4">
          <v-toolbar-title>{{activeTeacher.name}} {{activeTeacher.lastName}} rencileri</v-toolbar-title></h4>
     </template>
     <template v-slot:header="{ props: { headers } }">
        <thead>
          <tr>
            <th :colspan="headers.length">
               <p class="text-md-right text-left">
                  <span class="d-block d-md-inline">
                    <v-icon
          color="primary"
          dark
          medium
        >mdi-pencil-box-multiple</v-icon> renci ve Veliyi Dzenle
                  </span>
                  <span  class="d-block d-md-inline">
                    <v-icon
          color="primary"
          dark
          medium
        >mdi-close-box</v-icon> renci ve veliyi Kaldr
                  </span>
         <span  class="d-block d-md-inline">
             <v-icon
         color="success"
          dark
          medium

        >mdi-checkbox-marked</v-icon> Veliyi sistemde tekrar aktif et
         </span>
       
               </p>
            </th>
          </tr>
        </thead>
      </template>
    <template v-slot:item.edit="{ item }">
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="setEditUser(item,'student')">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>renciyi Dzenle</span>
        </v-tooltip>
         <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="textConfirm = `${item.name} ${item.lastname} isimli renciyi sistemden kaldryorsunuz. Emin Misiniz?`;processData={...item,type:'student'};dialog3=true">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-close-box</v-icon>
        </span>
      </template>
      <span>renciyi Kaldr</span>
        </v-tooltip>
    </template>
    <template v-slot:item.editParent="{ item }">
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="setEditUser(item,'user')">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Veliyi Dzenle</span>
        </v-tooltip>
        <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="textConfirm = `${item.parentActive==1?`${item.parentName} ${item.parentLastname} isimli veliyi sistemden kaldryorsunuz. Veliyi kaldrdnz taktirde ${item.name} ${item.lastname} isimli renciyi de kaldrm olacaksnz. Emin Misiniz?`:`${item.parentName} ${item.parentLastname} isimli veliyi tekrardan aktif etmek zeresiniz. Emin misiniz? `}`;processData={...item,type:'user'};dialog3=true">
          <v-icon
          :color='item.parentActive==1?"primary":"success"'
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >{{item.parentActive==1?'mdi-close-box':'mdi-checkbox-marked'}}</v-icon>
        </span>
      </template>
      <span>{{item.parentActive==1?'Veliyi ve renciyi Kaldr':'Veli sistemde aktif olarak bulunmuyor. Veliyi Aktif hale getir.'}}</span>
        </v-tooltip>
    </template>
    </v-data-table>
    <v-pagination v-model="page" :length="pageCount"></v-pagination>
  </v-card>
      </v-col>
    </v-row>
  <loading-dialog :dialogLoading="dialogLoading">{{type}} Detay Ykleniyor</loading-dialog>
  <edit-user-dialog :user="editUser" @dialog="editDialog=$event" :dialog="editDialog" />
  <edit-student-dialog :teachers="teachers" :user="editUser" @dialog="editStudentDialog=$event" :dialog="editStudentDialog" />
  <confirm :text="textConfirm" :dialog="dialog3" @dialog="dialog3=$event" @cancel="dialog3=false" @confirm="deleteItem" />
      <snack-bar :snackbar="snackbar"></snack-bar>
  </v-container>
</template>

<script>
import loadingDialog from './dialogs/loadingDialogs'
import editUserDialog from './dialogs/editUserDialogs'
import editStudentDialog from './dialogs/editStudentDialogs'
export default {
components:{
  loadingDialog,
  editUserDialog,
  editStudentDialog
},
props:{
    item:{
        default : ()=>[],
        type : Array,
        required:true
    },
    activeTeacher:{
        default : ()=> {},
        type : Object,
        required:true
    }
},
  name: "Alerts",
watch:{
    page(data){
 this.initStudents(data)
    },
    search(data){
        if(data.length>3){
             this.loadingData=true
          this.$api.get('i/j/w/student/user?searchFields=student.name,student.lastname,t2.name,t2.lastName,t2.phone&search='+data+'&filter=student.teacherId="'+this.activeTeacher.id+'"&on=student.parentId=t2.id&fields=student.*,t2.name AS parentName,t2.lastname AS parentLastname,t2.phone,t2.isActive AS parentActive&limit=10&offset=0').then(res=>{
          if(res.count!=0){
              this.loading = false
              res.body.forEach(result=>{
                if(result.parentActive==0){
                result.alert = true
              }
              else{
                result.alert = false
              }
              })
              this.item = res.body
              this.pageCount = res.count
                this.loadingData=false
          }
          else{
              this.pageCount = this.page
                this.loadingData=false
          }
      })
        }
    }
},
  data: () => ({
     dialogLoading:false,
     dialog3:false,
     snackbar:false,
     textConfirm:"",
     editDialog:false,
     processData:{},
     editStudentDialog:false,
      loading:true,
      type:"renci",
      page:1,
      loadingData:true,
      pageCount:0,
      student:[],
      teachers:[],
      headers: [
          {
            text: "renci Ad",
            align: 'start',
            filterable: true,
            value: 'name',
          },
          { text: 'Soyad', value: 'lastname' },
          { text: 'Doum Tarihi', value: 'birthday' },
           { text: 'Dzenle', value: 'edit' },
          { text: 'Veli Telefon', value: 'phone' , filterable: true,},
          { text: 'Veli Ad', value: 'parentName' },
          { text: 'Veli Soyad', value: 'parentLastname' },
          { text: 'Dzenle', value: 'editParent' },
        ],
        search:"",
        editUser:{}
  }),
  methods:{
   async  deleteItem(){
      let pos = this.item.map(function(e) { return e.id; }).indexOf(this.processData.id);
       if(this.processData.parentActive==1){
      if(this.processData.type=='student'){
        let student = await this.$api.put('student',this.processData.id,{isActive:0})
        if(student.result==1){
        this.snackbar=true
        this.item.splice(pos,1)
        this.dialog3 = false
			this.processData = null
         setTimeout(() => {
           this.snackbar=false
         }, 4000);
      }
      else{
            this.processData = null
         
      }
      }
      else{
        let parent = await this.$api.put('user',this.processData.parentId,{isActive:0})
        if(parent.result==1){
          let student = await this.$api.put('student',this.processData.id,{isActive:0})
        if(student.result==1){
        this.snackbar=true
        this.item.splice(pos,1)
        this.dialog3 = false
			this.processData = null
         setTimeout(() => {
           this.snackbar=false
         }, 4000);
        }
      }
    }
       }
       else{
        let parent = await this.$api.put('user',this.processData.parentId,{isActive:1})
        if(parent.result==1){
          this.item[pos].parentActive=1
          this.snackbar=true
        this.dialog3 = false
			this.processData = null
         setTimeout(() => {
           this.snackbar=false
         }, 4000);
        }

       }
  },
    setEditUser(row,type){
      
        if(type=='user'){
           this.type = "Veli"
           this.editUser = {
             name : row.parentName,
             lastName : row.parentLastname,
             id:row.parentId,
             username : row.username,
             phone:row.phone,
             isTeacher:0
           }
        }
         
        else if(type == 'student'){
           this.type = "renci" 
           this.editUser = row
        }
         
      this.dialogLoading = true
      setTimeout(() => {
        this.dialogLoading = false
        if(type=='user')
        
          this.editDialog = true
        else if(type == 'student')
          this.editStudentDialog = true 
      }, 1000);
    },
      initTeachers(){
        this.$api.get('user?filter=isTeacher=1&limit=100').then(res=>{
          if(res.count!=0){
            res.body.forEach(t=>{
              t.fullName = t.name +' '+ t.lastName
            })
            this.teachers = res.body
          }
          else{
            this.teachers =[]
          }
        })
      }
  },
  mounted(){
      this.initTeachers()
  }
};
</script>
